import React, { useEffect, useState } from 'react';
import axios from 'axios';

export default function UserItem(props){
    const user = props.user;
    console.log(user);
    const [toptag, setToptag] = useState([]);
    useEffect(()=>{
        const url = `https://api.stackexchange.com/2.2/users/${user.user_id}/top-tags?pagesize=6&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`
        axios.get(url).then((resp)=>{
            setToptag(resp.data.items);
        })
    }, [window.location.href])
    return(
        <div className='user-body'>
            <div className='user-info'>
                <img className='user-info-img' src={user.profile_image} alt='profile_image'/>
                <div className='user-info-2'>
                    <a href={'#/profile/'+user.user_id} dangerouslySetInnerHTML={{__html:user.display_name}}></a>
                    <p>{user.location}</p>
                    <p>Reputation: {user.reputation > 10000 ? (user.reputation/1000).toFixed(2) + 'k' : user.reputation}</p>
                </div>
            </div>
            <div className='user-tags'>
                {toptag.map((tag,index)=>{
                    return(
                        <p key={index}>{tag.tag_name}&nbsp;{tag.answer_score + tag.question_score}</p>
                    )
                })}
            </div>
            
        </div>
    )
}