import React, {useState, useEffect} from 'react';
import axios from 'axios';

export default function Tags() {
    const [tags, setTags] = useState([]);
    const [tagsWiki, setTagsWiki] = useState([]);
    let tagNames = '';
    useEffect(() => {
        axios.get(`https://api.stackexchange.com/2.2/tags?order=desc&sort=popular&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
            .then((resp) => {
                setTags(resp.data.items);
                tags.map(tag => {
                    tagNames += `${tag.name};`
                })
                tagNames = tagNames.substring(0, tagNames.length-1);
                console.log(tagNames);
            })
        axios.get(`https://api.stackexchange.com/2.2/tags/${tagNames}/wikis?site=stackoverflow&filter=!9_bDDrGXY&key=eZoZLL*)llKhER8pMMusAw((`)
            .then((resp)=>{
                setTagsWiki(resp.data.items);
            })
    }, [window.location.href])
    return (
        <div>
            {tags.map((tag, index)=>{
                return(
                    <div key={index} >
                        <p className='tag-name'>{tag.name}</p>
                        <p className='tag-count'>{tag.count}</p>
                        <p className='tag-description'>{tagsWiki[index].excerpt}</p>
                    </div>
                )
            })}
        </div>
    )
}