import React, { useEffect} from 'react';
import axios from 'axios';
import { __RouterContext } from 'react-router';
import QuestionItem from '../Components/QuestionItem';

function Home (props){
        const [questions, setQuestions] = React.useState([]);
        const tagged = '';
        const sortType = '';
        const search = '';
        if(props){ 
          props.tagged ?
          axios.get(`https://api.stackexchange.com/2.2/search?order=desc&sort=activity&tagged=${tagged}&site=stackoverflow&filter=!9_bDDxJY5&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setQuestions(resp.data.items);
          }) : 
          props.sort ?
          axios.get(`https://api.stackexchange.com/2.2/questions?order=desc&sort=${sortType}&site=stackoverflow&filter=!9_bDDxJY5&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setQuestions(resp.data.items);
          }) :
          props.search ?
          axios.get(`https://api.stackexchange.com/2.2/search?order=desc&sort=activity&intitle=${search}&site=stackoverflow&filter=!9_bDDxJY5&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setQuestions(resp.data.items);
          }) :
          console.log('1');
        } else {
          axios.get(`https://api.stackexchange.com/2.2/questions?order=desc&sort=activity&site=stackoverflow&filter=!9_bDDxJY5&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setQuestions(resp.data.items);
          })
        }

        return (
            <div>
                <h1>Main page</h1>
                {questions.map((question, index)=>{
                  return(
                    <QuestionItem question={question} key={index} />
                  )
                })}
            </div>
        )
    
}

export default Home;