import React, {useEffect, useState} from 'react';
import axios from 'axios';

export default function AssociateUser(props) {
    const [user, setUser] = useState([]);
    const userId = props.associate.user_id;
        useEffect(()=>{
            axios.get(`https://api.stackexchange.com/2.2/users/${userId}?order=desc&sort=reputation&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
            .then((resp)=>{
                setUser(resp.data.items);
            })
        }, [window.location.href])  
    console.log(user);
        return (
            <>
                {user.map((auser, index) => {
                    return(
                        <div key={index} className='associated-user' onClick={() => window.location.href=`#/profile/${auser.user_id}`}>
                            <img src={auser.profile_image} />
                            <p dangerouslySetInnerHTML={{__html:auser.display_name}}></p>
                        </div>
                    )
                })}
                </>
        )
}
