import React, { useState } from 'react';

export default function QuestionItem(props){
    const question = props.question;
    const [isShown, setIsShown] = useState(false);

    function timeConverter(UNIX_timestamp){
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
        return time;
      }
    
    return(
              <div className={'question-content' + ' ' +(isShown ? 'active' : '')} onMouseEnter={() => setIsShown(true)}
              onMouseLeave={() => setIsShown(false)}>
                <div onClick={()=>{window.location.href=`/question/${question.question_id}`}} className='votes'> 
                    <div>
                      <span>{question.score} </span>
                      <span>votes</span>
                    </div>
                    <div className={question.answer_count > 0 ? (question.is_answered ? 'answered' : 'some_answer') : 'no_answer'}>
                      <span>{question.answer_count} </span>
                      <span>answers</span>
                    </div>
                    <div>
                      <span>{question.view_count} </span>
                      <span>views</span>
                    </div>
                </div>
                <div className='question-info'>
                  <h3>
                    <a href={`#/question/${question.question_id}`} dangerouslySetInnerHTML={{__html:question.title}}></a>
                  </h3>
                  <div className='div-tag-author'>
                  <div className='home-tags'>
                    {question.tags.map((tag, index) =>{
                      return(
                        <a key={index} className='home-tags-tag'>{tag}</a>
                      )
                    })}
                  </div>
                  <div className='question-author-info'>
                    <a>changed&nbsp;
                      <span>{timeConverter(question.last_activity_date)}&nbsp;</span>
                    </a>
                    <a href={`#/profile/${question.owner.user_id}`} className='author' dangerouslySetInnerHTML={{__html:question.owner.display_name}}></a>
                    <span> reputation: {(question.owner.reputation >= 10000) ? (question.owner.reputation/1000).toFixed(1) + 'k' : question.owner.reputation}</span>
                  </div>
                  </div> 
                </div>
                {isShown &&
                  <div className='absolute-question' dangerouslySetInnerHTML={{__html:question.body}}></div>
                }
              </div>
            )
}
