import React, { useEffect } from 'react';
import axios from 'axios';
 
function Question(props) {  
    let qId = props.match.params.id;
    const [question, setQuestion] = React.useState([]);
    const [answers, setAnswers] = React.useState([]);
    const [axErr, setAxErr] = React.useState('');
    useEffect(() => {
        const qUrl = `https://api.stackexchange.com/2.2/questions/${qId}?order=desc&sort=activity&site=stackoverflow&filter=!3zl2.BHmiROa9.Phi&key=eZoZLL*)llKhER8pMMusAw((`;
        axios.get(qUrl).then((resp) => {
            const questionData = resp.data;
            console.log(questionData);
            
            setQuestion([...question, ...questionData.items]);
          },
          (error) => {
              setAxErr('download api error');
              console.log(error);
          }
          );
          axios.get(`https://api.stackexchange.com/2.2/questions/${qId}/answers?order=desc&sort=activity&site=stackoverflow&filter=!b6Aubk6cwNOqa9&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setAnswers(resp.data.items);
          },
          (error) => {
            setAxErr('download api error');
          }
          )
        }, [window.location.href])
        
        function timeConverter(UNIX_timestamp){
            var a = new Date(UNIX_timestamp * 1000);
            var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
            var year = a.getFullYear();
            var month = months[a.getMonth()];
            var date = a.getDate();
            var hour = a.getHours();
            var min = a.getMinutes();
            var sec = a.getSeconds();
            var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
            return time;
          }

        return (
            <>
            <div className='question-main'>
                {axErr.length ? <h1>{axErr}</h1> : 
                    question.map((q, index) => {
                    return (
                        <>
                        <div>
                            <h2 dangerouslySetInnerHTML={{__html:q.title}}></h2>
                            <div>
                                <span>created: {timeConverter(q.creation_date)} </span>
                                <span>Veiwed: {q.view_count}</span>
                            </div>
                        </div>
                        <div className='question_post' dangerouslySetInnerHTML={{__html:q.body}}>
                        </div>
                        <div className='question-comments'>
                          {q.comments ? q.comments.map((comment, index) =>{
                          return(
                            <div className='question-comments-item' key={index}>
                              <p className='question-comments-body' dangerouslySetInnerHTML={{__html:comment.body}}></p>
                              <p className='question-comments-author' dangerouslySetInnerHTML={{__html:comment.owner.display_name}} href={'#/profile/'+comment.owner.user_id}></p>
                            </div>
                          )
                        }) :
                        <>
                        </>
                          }
                        </div>
                        <div>{q.tags.map((tag,index) => {
                            return (
                            <span key={index} className='question-tags'>{tag}</span>
                            )
                        })}
                        </div>
                        <div className='profile-info-question'>
                            <p>asked {timeConverter(q.creation_date)}</p>
                            <div className='profile-info-question-2'>
                                <img className='profile-image' src={q.owner.profile_image} alt='prolife image'></img>
                                <div>
                                    <a href={`#/profile/${q.owner.user_id}`} dangerouslySetInnerHTML={{__html:q.owner.display_name}}></a>
                                    <p>{q.owner.reputation}</p>
                                </div>
                            </div>
                        </div>
                        </>
                    )
                })
                }
            </div>
            <div className='answer-main'>
                {answers.map((answer, index) => {
                    return(
                        <div key={index} className='answer'>
                            <div className='answer-score'>
                                <p>{answer.score}</p>
                            </div>
                            <div className='answer-body'>
                                <p dangerouslySetInnerHTML={{__html:answer.body}}></p>
                                <div className='answer-comments'>
                                  { answer.comments ?
                                    answer.comments.map((comment, index)=>{
                                      return(
                                        <div className='answer-comments-item' key={index}>
                                          <p className='answer-comments-body' dangerouslySetInnerHTML={{__html:comment.body}}></p>
                                          <p className='answer-comments-author' dangerouslySetInnerHTML={{__html:comment.owner.display_name}} href={'#/profile/'+comment.owner.user_id}></p>
                                        </div>
                                      )
                                    }):
                                    <></>
                                  }
                                </div>
                                <div className='profile-info-question'>
                                <p>{timeConverter(answer.creation_date)}</p>
                                <div className='profile-info-question-2'>
                                    <img className='profile-image' src={answer.owner.profile_image} alt='profile_image'/>
                                    <div>
                                        <a href={'#/profile/'+answer.owner.user_id} dangerouslySetInnerHTML={{__html:answer.owner.display_name}}></a>
                                        <p>{answer.owner.reputation}</p>
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                    )
                })}
            </div>
            </>
        )
    }

export default Question;