import React, { useState } from 'react';
import { Nav, Navbar, Form, FormControl, Button, Container} from 'react-bootstrap';
import logo from '../logo.svg';
import axios from 'axios';
import Question from '../Pages/Question';
import Home from '../Pages/Home';
import { Switch, Route } from 'react-router-dom';
import Users from '../Pages/Users';
import Profile from '../Pages/Profile';
import Tags from '../Pages/Tags';
import Search from '../Pages/Search';
import Tagged from '../Pages/Tagged';

export default function Header(){
    const [islogin, setIslogin] = useState(false);
    const [me, setMe] = useState([]);
    const [logininfo, setLogininfo] = useState();
    const [value, setValue] = useState('');
    const [isSearch, setIsSearch] = useState(false);

        function login(){
            window.SE.authenticate({
                success: function(data){
                    setIslogin(true);
                    setLogininfo(data);
                    console.log(data);
                    axios.get(`https://api.stackexchange.com/2.2/users/${data.networkUsers[0].user_id}?order=desc&sort=reputation&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
                    .then((resp) => {
                        setMe(resp.data.items)
                    })
                    console.log(me)
                },
                error: function(data){
                    alert('An error occurred:\n' + data.errorName + '\n' + data.errorMessage)
                },
                networkUsers: true
            });
        }
        function logout(token){
            axios.get(`https://api.stackexchange.com/2.2/access-tokens/${token}/invalidate?key=eZoZLL*)llKhER8pMMusAw((`)
            setIslogin(false);
        }

        function submitHandler(event){
            event.preventDefault()
            if(value.trim()){
                setIsSearch(true);
            }
        }

        return(
            <>
            <Navbar bg='dark' variant='dark' collapseOnSelect expand='md'>
                <Container>
                    <Navbar.Brand href='#/'>
                        <img 
                            alt='logo'
                            src={logo}
                            width='30'
                            height='30'
                            className='d-inline-block align-top'
                        />
                        USOF
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                    <Navbar.Collapse id='responsive-navbar-nav'>
                        <Nav className='mr-auto'>
                            <Nav.Link href='#/'>Main</Nav.Link>
                            <Nav.Link href='#/users'>Users</Nav.Link>
                            <Nav.Link href='#/tags'>Tags</Nav.Link>
                        </Nav>
                        <Form inline onSubmit={submitHandler}>
                            <FormControl type='text' placeholder='Search' className='mr-sm-2' value={value} onChange={event => setValue(event.target.value)}/>
                            <Button variant='outline-success' href={isSearch ? '#/search/'+value.trim() : ''} >Search</Button>
                        </Form>                         
                            {islogin ? 
                                <>
                                <div className='my-info'>
                                    {me.map(myp => {
                                        return(
                                            <>
                                            <img className='my-image' src={myp.profile_image} alt='profile image' />
                                            <p className='my-name' href={'#/profile/'+me[0].user_id} dangerouslySetInnerHTML={{__html:myp.display_name}}></p>
                                            </>
                                        )
                                    })}
                                </div>
                                <Button variant='outline-info' className='ml-3' onClick={() => logout(logininfo.accessToken)}>Logout</Button>
                                </>
                                : 
                                <Button variant='outline-info' className='ml-3' onClick={() => login()}>Login</Button>
                            }
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <Switch>
                    <Route exact path='/' component={Home} />
                    <Route path='/search/:info' component={Search} />
                    <Route path='/tagged/:tag' component={Tagged} />
                    <Route path='/users' component={Users} />
                    <Route path='/tags' component={Tags} />
                    <Route path='/question/:id' component={Question} />
                    <Route path='/profile/:user_id' component={Profile} />
                </Switch>
            
            </>
        )
    }


    // /2.2/access-tokens/{accessTokens}/invalidate