import React from 'react';

export default function Posts(props){

    const postState = props.post;
    const postType = props.postType;

    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
        return time;
      }

    return(
        <div>
                {
                postState.map((post, index) => {
                    return(
                        <>
                    <div key={index}>{
                        postType === 'qa' ? 
                        post.post_type : postType === 'questions' ? 
                        'Question' : postType === 'answers' ?
                        'Answer' : post.post_type
                        }: {post.score}</div>
                    <div dangerouslySetInnerHTML={{__html:post.title}}></div>
                    </>
                    // <div key={index}>{index}</div>
                    )
                })
                
                // postType === 'qa' ? 
                // <div>{timeConverter()}</div> :
                // postType === 'questions' ? 
                // <div>Questions</div> :
                // postType === 'answers' ?
                // <div>Answers</div> :
                // postType === 'favorites' ?
                // <div>Favorites</div> :
                // <div>Something going wrong</div>
            }
        </div>
    )
}